import dayjs from 'dayjs';
import relativeTime from 'dayjs/plugin/relativeTime.js';

/**
 *         .
 *  - https://github.com/you-dont-need/You-Dont-Need-Lodash-Underscore#_random
 * @param {number} min  
 * @param {number} max  
 * @returns {number}     
 */
export const getRandomNumber = (min, max) => {
  const lower = Math.ceil(Math.min(min, max));
  const upper = Math.floor(Math.max(min, max));

  return Math.floor(lower + Math.random() * (upper - lower + 1));
};

/**
 *      - .
 *  - https://en.wikipedia.org/wiki/Fisher%E2%80%93Yates_shuffle
 * @param {Array} array   
 * @returns {Array}  
 */
export const shuffleArray = (array) => {
  const newArray = array.slice();

  for (let i = newArray.length - 1; i > 0; i--) {
    const j = getRandomNumber(0, i);

    [newArray[i], newArray[j]] = [newArray[j], newArray[i]];
  }

  return newArray;
};

/**
 * @param {string} date    ISO 8601
 * @returns {string}   
 */
export const formatIsoDate = (date, template) => dayjs(date).format(template);

/**
 *
 * @param {string} date    ISO 8601
 * @returns {string}    "2  "
 */
export const dateFromNow = (date) => {
  dayjs.extend(relativeTime);
  return dayjs(date).fromNow();
};

/**
 * @param {number} runtime   
 * @returns {string}     "1h 3m"
 */
export const formatRuntime = (runtime) => {
  const hours = Math.floor(runtime / 60);
  const minutes = runtime - (hours * 60);

  return `${hours}h ${minutes}m`;
};

/**
 *     
 * @param {string} string ,      
 * @returns {string}     
 */
export const capitalizeFirstLetter = (string) => (
  string[0].toUpperCase() + string.slice(1).toLowerCase()
);

/**
 *       c  
 * @param {number} number ,   
 * @returns {string}   ,   
 * @example
 * splitNumberToClasses(458654); // > '458 654'
 */
export const splitNumberToClasses = (number) => number.toLocaleString();

